/**
 * @todo
 * @param index - –∏–Ω–¥–µ–∫—Å –ø–æ–ª—è
 * @param boardSize - —Ä–∞–∑–º–µ—Ä –∫–≤–∞–¥—Ä–∞—Ç–Ω–æ–≥–æ –ø–æ–ª—è (–≤ –¥–ª–∏–Ω—É –∏–ª–∏ —à–∏—Ä–∏–Ω—É)
 * @returns —Å—Ç—Ä–æ–∫–∞ - —Ç–∏–ø —è—á–µ–π–∫–∏ –Ω–∞ –ø–æ–ª–µ:
 *
 * top-left
 * top-right
 * top
 * bottom-left
 * bottom-right
 * bottom
 * right
 * left
 * center
 *
 * @example
 * ```js
 * calcTileType(0, 8); // 'top-left'
 * calcTileType(1, 8); // 'top'
 * calcTileType(63, 8); // 'bottom-right'
 * calcTileType(7, 7); // 'left'
 * ```
 * */
export function calcTileType(index, boardSize) {
  if (index >= boardSize ** 2 || index < 0) {
    return 'invalid value';
  }
  if (index === 0) {
    return 'top-left';
  }
  if (index === boardSize - 1) {
    return 'top-right';
  }
  if (index === boardSize ** 2 - boardSize) {
    return 'bottom-left';
  }
  if (index === boardSize ** 2 - 1) {
    return 'bottom-right';
  }
  if (index % boardSize === 0) {
    return 'left';
  }
  if ((index + 1) % boardSize === 0) {
    return 'right';
  }
  if (index > 0 && index < boardSize) {
    return 'top';
  }
  if (index < (boardSize) ** 2 - 1 && index > boardSize ** 2 - boardSize) {
    return 'bottom';
  }

  return 'center';
}

export function calcHealthLevel(health) {
  if (health < 15) {
    return 'critical';
  }

  if (health < 50) {
    return 'normal';
  }

  return 'high';
}
