/**
 * –ë–∞–∑–æ–≤—ã–π –∫–ª–∞—Å—Å, –æ—Ç –∫–æ—Ç–æ—Ä–æ–≥–æ –Ω–∞—Å–ª–µ–¥—É—é—Ç—Å—è –∫–ª–∞—Å—Å—ã –ø–µ—Ä—Å–æ–Ω–∞–∂–µ–π
 * @property level - —É—Ä–æ–≤–µ–Ω—å –ø–µ—Ä—Å–æ–Ω–∞–∂–∞, –æ—Ç 1 –¥–æ 4
 * @property attack - –ø–æ–∫–∞–∑–∞—Ç–µ–ª—å –∞—Ç–∞–∫–∏
 * @property defence - –ø–æ–∫–∞–∑–∞—Ç–µ–ª—å –∑–∞—â–∏—Ç—ã
 * @property health - –∑–¥–æ—Ä–æ–≤—å–µ –ø–µ—Ä—Å–æ–Ω–∞–∂–∞
 * @property type - —Å—Ç—Ä–æ–∫–∞ —Å –æ–¥–Ω–∏–º –∏–∑ –¥–æ–ø—É—Å—Ç–∏–º—ã—Ö –∑–Ω–∞—á–µ–Ω–∏–π:
 * swordsman
 * bowman
 * magician
 * daemon
 * undead
 * vampire
 */
export default class Character {
  constructor(level, type = 'generic') {
    this.level = level;
    this.attack = 0;
    this.defence = 0;
    this.health = 50;
    this.type = type;
    if (new.target === Character) {
      throw new Error('Invalid action');
    }
    // TODO: –≤—ã–±—Ä–æ—Å–∏—Ç–µ –∏—Å–∫–ª—é—á–µ–Ω–∏–µ, –µ—Å–ª–∏ –∫—Ç–æ-—Ç–æ –∏—Å–ø–æ–ª—å–∑—É–µ—Ç "new Character()"
  }

  levelUp(level = 1) {
    for (let i = 1; i < level; i += 1) {
      this.improveAttack();
      this.improveDefence();
      this.heal();
    }
  }

  heal() {
    this.health += 80;
    if (this.health > 100) {
      this.health = 100;
    }
  }

  improveAttack() {
    this.attack = Math.max(this.attack, Math.floor(this.attack * ((80 + this.health) / 100)));
  }

  improveDefence() {
    this.defence = Math.max(this.defence, Math.floor(this.defence * ((80 + this.health) / 100)));
  }
}
